:root{
  --Nav-font-size: 1.2em
}

*:focus{
  outline: 1px solid rgb(223, 52, 0);
}
*{
  color:white;
  margin: 0;
  padding: 0;
  
}
body{
  background-color: #222222;
  -khtml-user-select: none;
    -o-user-select: none;
    -moz-user-select: none;
    -webkit-user-select: none;
    user-select: none;
}
#root{
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  width: 95vw;
  margin: auto;
}

#Header{
  font-size: var(--Nav-font-size);
  box-sizing: border-box;
  margin:0 auto;
  width: 94.5vw;
  min-height: 55px;
  height: 5vw;
  display: flex;
  align-items: center;
  justify-content: space-between;
  z-index: 99;
}
#HeaderLeft{
  display: flex;
}
#HeaderRight{
  display: flex;
}
#NavHeader-ul{
  list-style: none;
  display: flex;
}
#NavHeader-ul li{margin-right: 10px;}
#NavHeader-ul a{
  padding: 5px 12px;
  text-decoration: none;
  display: block;
}
#NavHeader-ul a:hover{
  background-color: #1a1a1a;
}
#Logo{
  min-width: 40px;
  min-height: 40px;
  width: 3.1vw;
  height: 3.1vw;
}
#divImg{
  min-width: 40px;
  min-height: 40px;
  width: 3.1vw;
  height: 3.1vw;
  background-color: white;
  border-radius: 100%;
  margin-right: 10px;
}

#Search{
  background-color: #1a1a1a;
  border-radius: 5px;
  position: relative;
  display: block;
  padding: 5px 12px;
  text-decoration: none;
  padding-left: 36px;
}
#Search::after{
  position: absolute;
  top: 4px;
  left: 6px;
  content: " ";
  display: block;
  background: url("./Orion_search-magnify.svg");
  width: 30px;
  height: 30px;
  background-size: 30px 30px;
}
#divAvatar{
  position: relative;
}
#Avatar{
  background: none;
  border: none;
}
#Avatar img{
  min-height: 35px;
  min-width: 35px;
  width: 2.65vw;
  height: 2.65vw;
}

#AvatarBar{
  z-index: 99;
  font-size: .9em;
  position: absolute;
  top: 120%;
  right: 0;
  background: #1a1a1a;
  padding: 10px;
}
#AvatarBar a, #Login {
  border-radius: 5px;
  padding: 5px 12px;
  text-decoration: none;
  display: block;
}
#AvatarBar a:hover{background-color: #141414;}
#Login{ background-color: #ce5200; }
#Login:hover{ background-color: #b64900; }





#Error {
  width: 94vw;
  text-align: center;
  height: auto;
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
}
#Error p{
  font-size: 1.3em;
}
#Error h1{
  font-size: 3em;
}





#MainSlider{
  overflow: hidden;
  margin: 1vw 0 5vw 0;
  width: 100%;
  user-select: none;
  min-height: 270px;
  height: 29vw;

}

#MainSliderContainer{
  min-height: 175px;
  height: 24.76vw;
  transition: .5s;
  position: relative;
  display: flex;
}
.MainSliderChild{
  background-color: #1f1f1f;
  min-width: 100%;
}
.MainSliderChild a{
  outline: none;
  text-decoration: none;
  height: 100%;
  cursor: default;
  display: flex;
  align-items: center;
}

.MainSliderChild img{
  background-color: #141414;
  width: 21vw;
  height: 100%;
}
.MainSliderChild div{
  overflow: hidden;
  pointer-events: none;
  min-height: 175px;
  height: 24.76vw;
  width: 100%;
  padding: 1%;
  box-sizing: border-box;
}
.MainSliderChild div h2, .MainSliderChild div p{
  margin: 0px;
  padding: 0px;
}
.dot{
  display: block;
  min-width: 25px;
  min-height: 8px;
  width: 1.8vw;
  height: .8vw;
  background: transparent;
  border-radius: 1vw;
  border: 1px solid white;
  margin-right: 1%;
}
#Dots{
  display: flex;
  min-height: 40px;
  height: 4vw;
  box-sizing: border-box;
  min-width: 190px;
  width: 13vw;
  margin: 0 auto;
  justify-content: space-between;
  align-items: center;
}

.dot:focus{
  background: #a54200;
  outline: none;
}
.dot.focus{
  background: #ff6600;
}


#HotTitles{
  background-color: #1d1d1d;
  display: flex;
  width: 100%;
}
#HotTitlesHeader{
  font-size: 1.1em;
  display: table-cell;
  vertical-align: bottom;
  height: 7.5vw;
}
#HotTitlesHeader h2{
  min-height: 70px;
}
#HotTitlesTop{
  position: relative;
  overflow: hidden;
  width: 40%;
  height: 14.85vw;
  min-height: 200px;
}
#HotTitlesSlider{
  background-color: #1d1d1d;
  overflow: hidden;
  width: 60%;
  height: 14.85vw;
  min-height: 200px;
}
#HotTitlesSliderContainer{
  transition-timing-function: linear;
  height: 100%;
  display: flex;
  align-items: center;
  width:100%;
}
#HotTitlesTopContainer{
  transition: .3s;
  height: 100%;
  display: flex;
  justify-content: space-around;
  align-items: center;
}
#HotTitlesTopContainer a{
  display: block;
}
.HotTitlesTopChild img{
  transition: .3s;
  display: block;
  min-width: 90px;
  min-height: 130px;
  width: 8.304vw;
  height: 12.1vw;
  background-color: #141414;
}
.HotTitlesTopChildFocus a:focus, .HotTitlesTopChild a:focus{
  outline: 2px solid rgb(223, 52, 0);
}
.HotTitlesTopChildFocus img{
  transition: .3s;
  display: block;
  min-width: 120px;
  min-height: 165px;
  width: 10.4917vw;
  height: 14.35vw;
  background-color: #141414;
}
.HotTitlesTopDescription{
  text-decoration: none;
  box-sizing: border-box;
  background-color: #141414;
  transition: .5s;
  height: 100%;
  padding: 25px;
  width: 100%;
  position: absolute;
  top: 100%;
  left: 0;
}
.HotTitlesSliderChild{
  flex: 0 0 auto;
  width: 11vw;
  min-width: 130px;
}

.HotTitlesSliderChild img{
  width: 8.304vw;
  height: 12.1vw;
  min-width: 90px;
  min-height: 130px;
  background-color: #141414;
}

#RankingTitlesContainer{
  margin-top: 2vw;
  overflow-x: auto;
  display: flex;
  justify-content: space-between;
}
.RankingTitlesChild{
  flex: 0 0 auto;
  min-width: 300px;
  width: 33%;
}
.RankingTitlesChild > div{
  min-height: 145px;
  height: 11vw;
}
.RankingTitlesChild > h2{
  min-height: 60px;
  display: flex;
  align-items: center;
}
.RankingTitlesChild a {
  text-decoration: none;
}
.RankingTitlesChild >div{
  font-size: .85em;
  display: flex;
  justify-content: space-between;
}
.RankingTitlesChild>div>div{
  width: 24vw;
  min-width: 205px;
  box-sizing: border-box;

}
.RankingTitlesChild img{
  min-width: 85px;
  min-height: 120px;
  width:6.422vw;
  height:9.06vw
}

.RankingTitlesInfo{
  width: 100%;
  height: 7vw;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.RankingTitlesInfo > div:first-child{
  overflow-x: hidden;
}

.RankingTitlesInfo > div:nth-child(2) > div:first-child{
  color: #727272;
}
.RankingTitlesInfo>div:nth-child(2) >div:last-child{
  color: #9b4b00;
}
.RankingTitlesSliderGenres{
    display: flex;
}
.RankingTitlesSliderGenres > div{
  flex: 0 0 auto;
  width: 8.5vw;
  min-width: 90px;
}
.RankingTitlesSliderGenres a{
  display: block;
  padding: 1px;
  border-radius: 1em;
  min-width: 70px;
  width: 7.8vw;
  text-align: center;
  background-color: #9b4a14a8;
}








@media screen and (max-width:500px) {
  #Dots {
    margin-top: 5px;
  }
  #HotTitlesHeader h2{
    min-height: 50px;
  }
}
@media screen and (max-width:700px) {
  .MainSliderChild img{
    width: 100px;
    height: 135px;
  }
  #MainSlider {
    font-size: .8em;
  }
  #HotTitles {
    display: block;
  }
  #HotTitlesTop{
    min-width: 100%;
  }
  #HotTitlesSlider{
    min-width: 100%;
  }
    #HotTitlesHeader{
      font-size: 1.1em;
    }
}
@media screen and (max-width: 768px) {
  #HotTitlesSliderContainer{
    overflow-x: scroll;
    
  }
  #MainSlider {
    font-size: .85em;
  }
  .HotTitlesTopDescription {
    font-size: .85em;
  }
  .RankingTitlesChild{
    font-size: .9em;
  }
  .RankingTitlesInfo>div:first-child{
    overflow-x: auto;
    min-height: 40px;
  }
}
@media screen and (max-width: 1400px) {
  .HotTitlesTopDescription {
    font-size: 1.05em;
  }
}
@media screen and (max-width: 1200px) {
  .HotTitlesTopDescription {
    font-size: 1em;
  }
  .RankingTitlesChild{
    padding-right: 8px;
  }
}
@media screen and (max-width: 992px) {
  .HotTitlesTopDescription {
    font-size: .98em;
  }
}

@media screen and (max-width: 576px) {
  .HotTitlesTopDescription{
    font-size: .8em;
  }
}


.pointEvent{
  pointer-events: none;
}





